import React, { useState } from "react";
import { Link, useHistory, useLocation } from "react-router-dom";
import { Input } from "@ya.praktikum/react-developer-burger-ui-components";
import styles from "./pages.module.css";
import { Button } from "@ya.praktikum/react-developer-burger-ui-components";
import { loginUser } from "../services/user/actions";
import { useAppDispatch } from "../hooks/hooks";
import { Location } from "history";

const Login: React.FC = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [passwordVisible, setPasswordVisible] = useState(false);
  const dispatch = useAppDispatch();
  const history = useHistory();
  const location: any = useLocation<Location>();

  const submitHandler = (e: React.FormEvent) => {
    e.preventDefault();
    dispatch(
      loginUser(email, password, () => {
        const path = location?.state?.afterLogin || "/";
        history.push(path);
      })
    );
  };

  return (
    <section className={styles.section}>
      <form className={styles.form} onSubmit={submitHandler}>
        <h1 className="text text_type_main-medium mt-6"></h1>
        <div className={styles.input}>
          <Input
            type="email"
            placeholder="E-mail"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            size="small"
          />
        </div>
        <div className={styles.input}>
          <Input
            type={passwordVisible ? "text" : "password"}
            placeholder=""
            icon={passwordVisible ? "HideIcon" : "ShowIcon"}
            onIconClick={() => {
              setPasswordVisible(!passwordVisible);
            }}
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            size="small"
          />
        </div>
        <div className="mt-6">
          <Button type="primary" size="small">
            
          </Button>
        </div>
        <div className="mt-20 text text_type_main-default">
          <span className="m-2 text_color_inactive">
              ?
          </span>
          <Link to="/register" className={styles.link}>
            
          </Link>
        </div>
        <div className="mt-4 text text_type_main-default">
          <span className="m-2 text_color_inactive"> ? </span>
          <Link to="/forgot-password" className={styles.link}>
             
          </Link>
        </div>
      </form>
    </section>
  );
};

export default Login;
